body{
  overflow-x: hidden;
  margin: 0px 0px 100px; /* bottom = footer height */
  background-image: url(/images/bg1.jpeg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-attachment: fixed;
}

.header-container{
  width: 100%;
  height: 800px;
  border-left: none;
  border-right: none;
  position: relative;
  padding: 20px;
}

.video-container{
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

video{
  position: absolute;
  z-index: -1;
  width: 100%;
}



